`, "",{"version":3,"sources":["webpack://./src/通用状态栏框架/components/statusbar/StatusBar.vue"],"names":[],"mappings":"AAqCA,6BACE,+BAAmC,CACnC,6BAAiC,CACjC,wCAA4C,CAE5C,sBAAuB,CACvB,wBAAyB,CACzB,uBAAwB,CAExB,uBAAwB,CACxB,uBAAwB,CAExB,+DAKY,CACZ,cAAe,CACf,yBAA0B,CAC1B,wBAAyB,CACzB,0BAA2B,CAC3B,kCAAmC,CACnC,kCAAmC,CACnC,kBAAmB,CACnB,iBAAkB,CAClB,eAAgB,CAChB,4BACF,CAEA,qCACE,YAAa,CACb,kBAAmB,CACnB,QAAS,CACT,mBAAoB,CACpB,kBAAmB,CACnB,yCACF,CAEA,mCACE,0BAA2B,CAC3B,aACF,CAEA,oCACE,eAAgB,CAChB,cAAe,CACf,yBACF,CAEA,wCACE,gBAAiB,CACjB,cAAe,CACf,eAAgB,CAChB,gBAAiB,CACjB,gCAAoC,CACpC,0BAA2B,CAC3B,kBAAmB,CACnB,kDACF,CAEA,0BACE,QAEE,SACF,CACA,IACE,WACF,CACF,CAEA,sCACE,YAAa,CACb,qBAAsB,CACtB,OACF,CAEA,0CACE,YAAa,CACb,qBAAsB,CACtB,kBAAmB,CACnB,OAAQ,CACR,YAAa,CACb,2BAA4B,CAC5B,iBACF,CAEA,mCACE,cAAe,CACf,0BACF","sourcesContent":["<script setup lang=\"ts\">\n/**\n * 状态栏组件 - 浅色模式版本\n * 显示在每个 AI 消息楼层下方\n */\nimport { injectStreamingMessageContext } from '@util/streaming';\n\nconst ctx = injectStreamingMessageContext();\n\n// SVG 图标路径\nconst databaseIcon =\n  'M12 2C6.48 2 2 4.69 2 8v8c0 3.31 4.48 6 10 6s10-2.69 10-6V8c0-3.31-4.48-6-10-6zm0 2c4.42 0 8 1.79 8 4s-3.58 4-8 4-8-1.79-8-4 3.58-4 8-4zm8 12c0 2.21-3.58 4-8 4s-8-1.79-8-4v-2.18c1.86 1.38 4.78 2.18 8 2.18s6.14-.8 8-2.18V16z';\n</script>\n\n<template>\n  <div class=\"status-bar\">\n    <div class=\"status-bar__header\">\n      <svg class=\"status-bar__icon\" viewBox=\"0 0 24 24\" width=\"18\" height=\"18\">\n        <path :d=\"databaseIcon\" fill=\"currentColor\" />\n      </svg>\n      <span class=\"status-bar__title\">通用状态栏</span>\n      <span v-if=\"ctx.during_streaming\" class=\"status-bar__streaming\">生成中...</span>\n    </div>\n    <div class=\"status-bar__content\">\n      <div class=\"status-bar__placeholder\">\n        <span>暂无状态数据</span>\n        <span class=\"status-bar__hint\">点击扩展菜单中的「通用状态栏管理器」添加定义</span>\n      </div>\n    </div>\n  </div>\n</template>\n\n<style scoped>\n/* ============================================\n   浅色模式状态栏 - 玻璃拟态风格\n   ============================================ */\n\n.status-bar {\n  --bar-bg: rgba(255, 255, 255, 0.92);\n  --bar-border: rgba(0, 0, 0, 0.08);\n  --bar-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);\n\n  --text-primary: #1f2937;\n  --text-secondary: #6b7280;\n  --text-tertiary: #9ca3af;\n\n  --color-primary: #6366f1;\n  --color-success: #10b981;\n\n  font-family:\n    'Outfit',\n    'Inter',\n    system-ui,\n    -apple-system,\n    sans-serif;\n  font-size: 14px;\n  color: var(--text-primary);\n  background: var(--bar-bg);\n  backdrop-filter: blur(12px);\n  -webkit-backdrop-filter: blur(12px);\n  border: 1px solid var(--bar-border);\n  border-radius: 12px;\n  padding: 14px 16px;\n  margin-top: 12px;\n  box-shadow: var(--bar-shadow);\n}\n\n.status-bar__header {\n  display: flex;\n  align-items: center;\n  gap: 10px;\n  padding-bottom: 12px;\n  margin-bottom: 12px;\n  border-bottom: 1px solid var(--bar-border);\n}\n\n.status-bar__icon {\n  color: var(--color-primary);\n  flex-shrink: 0;\n}\n\n.status-bar__title {\n  font-weight: 600;\n  font-size: 15px;\n  color: var(--text-primary);\n}\n\n.status-bar__streaming {\n  margin-left: auto;\n  font-size: 11px;\n  font-weight: 500;\n  padding: 3px 10px;\n  background: rgba(16, 185, 129, 0.12);\n  color: var(--color-success);\n  border-radius: 12px;\n  animation: pulse 1.5s ease-in-out infinite;\n}\n\n@keyframes pulse {\n  0%,\n  100% {\n    opacity: 1;\n  }\n  50% {\n    opacity: 0.5;\n  }\n}\n\n.status-bar__content {\n  display: flex;\n  flex-direction: column;\n  gap: 8px;\n}\n\n.status-bar__placeholder {\n  display: flex;\n  flex-direction: column;\n  align-items: center;\n  gap: 8px;\n  padding: 20px;\n  color: var(--text-secondary);\n  text-align: center;\n}\n\n.status-bar__hint {\n  font-size: 12px;\n  color: var(--text-tertiary);\n}\n</style>\n"],"sourceRoot":""}]);
